.App {
  text-align: center;
  background-color: #09f979;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #f4f5f8;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
img {
  display:inline-block;
  border-radius: 40%;
}
.head{
  background-color: #09f979;
  color:#0478ed;
}


ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow:hidden;
  border-style: dashed;
  border-color: #0478ed;

}
li{
  
  float:left;
  
}
li a{
  color:#0478ed;
  display:block;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
.active{
  background-color: #9da09f9d;
}

h3{
  color:#0478ed;
}